:root {
    --player-width: calc(100% * 0.65); 
}

.player-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 30;
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 1;
    overflow: auto;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
        visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &.is-hidden {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
    }
}

.player {
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);

    width: calc(100% * 0.65);
    padding-bottom: 37%;
}

.responsive-player {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.btn-player-close {
    position: absolute;
    top: -40px;
    right: -25px;

    width: 30px;
    height: 30px;
    padding: 0;
    border: none;
    background-color: transparent;

    &:hover {
        cursor: pointer;
    }
}
