*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.ligth-checkbox {
  transform: -50% -50%;
  position: absolute;
  top: 84%;
  left: 48%;
}

input[type="checkbox"] {
  width: 60px;
  height: 22px;
  -webkit-appearance: none;
  cursor: pointer;
  background: linear-gradient(0deg, rgba(234, 250, 4, .5), rgba(234, 250, 4, .1));
  border-radius: 20px;
  outline: none;
  position: relative;
  box-shadow: 0 0 2px #292929, 0 0 0 3px #353535, 0 0 0 3px #3e3e3e, inset 0 0 10px #000, 0 5px 20px rgba(0, 0, 0, .5), inset 0 0 15px rgba(0, 0, 0, .2);
}

input:checked[type="checkbox"] {
  background: linear-gradient(0deg, rgba(255, 107, 1, .467), #212201);
  box-shadow: 0 0 2px #292929, 0 0 0 3px #353535, 0 0 0 3px #3e3e3e, inset 0 0 10px #000, 0 5px 20px rgba(0, 0, 0, .5), inset 0 0 15px rgba(0, 0, 0, .2);
}

input[type="checkbox"]:before {
  content: "";
  width: 30px;
  height: 20px;
  z-index: 2;
  background: linear-gradient(0deg, #000, #6b6b6b);
  border-radius: 20px;
  transition: all .25s;
  position: absolute;
  top: 1px;
  left: 1px;
  transform: scale(.98, .96);
  box-shadow: 0 0 0 1px #232323;
}

input:checked[type="checkbox"]:before {
  left: 29px;
}

.moon-icon {
  width: 17px;
  height: 17px;
  pointer-events: none;
  fill: #585858;
  position: absolute;
  top: 2px;
  left: 6px;
}

.sun-icon {
  width: 17px;
  height: 17px;
  pointer-events: none;
  fill: #ff0;
  position: absolute;
  top: 2px;
  left: 37px;
}

.sun-icon .is-off {
  fill: #000;
}

.night {
  background: #272e34;
  transition: all .25s;
}

:root {
  --primary-text-color: #000;
  --second-text-color: #545454;
  --white-text-color: #fff;
  --light-text-color: #f9f9f9;
  --accent-color: #ff6b01;
  --accent-color-transparent: rgba(255, 107, 1, .52);
  --accent-color-transparenty: rgba(255, 107, 1, .686);
  --primary-text-color-night: #8c8c8c;
  --second-color-night: rgba(255, 107, 1, .8);
  --primary-color-night: rgba(255, 107, 1, .467);
  --background-color-night: #272e34;
}

.vissualy-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.modal-close-btn {
  flex-wrap: wrap;
  align-items: center;
  display: flex;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  background-color: var(--white-text-color);
  color: var(--primary-text-color);
  letter-spacing: .03em;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
}

ul, li, ol, address {
  margin: 0;
  padding: 0;
  list-style: none;
}

.section {
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}

img, picture {
  height: auto;
  margin: 0;
  padding: 0;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

.link {
  text-decoration: none;
  display: block;
}

.list {
  list-style: none;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding: 0 20px;
  display: block;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 30px;
