.top {
  z-index: 5;
  width: 50px;
  height: 50px;
  color: var(--accent-color-transparent);
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  border-radius: 50%;
  margin-left: auto;
  transition: all .2s;
  position: -webkit-sticky;
  position: sticky;
  bottom: 50px;
  right: 10px;
  transform: translateY(25px);
  box-shadow: inset 0 0 0 4px #dcdcdc;
}

.top:before {
  height: 100%;
  width: 100%;
  content: "🠩";
  justify-content: center;
  align-items: center;
  font-family: Roboto;
  font-size: 30px;
  font-weight: 500;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.top:focus, .top:hover {
  color: var(--accent-color);
}

.top--active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.top__svg--path {
  box-sizing: border-box;
  fill: none;
  stroke: var(--accent-color-transparenty);
  stroke-width: 4px;
  transition: all .2s;
}

